.menuPage {
   flex: 50% 1 1;
   // position: relative;
   .logo {
      position: absolute;
      top: 5px;
   }

   display: flex;
   justify-content: center;
   @media (max-width: 1430px) {
      flex: 40% 1 1;
   }
   @media (max-width: 1230px) {
      display: none;
      flex: 0 0 1;
   }
}

.menuPageOnHome {
   @media (max-width: 1230px) {
      display: inline-flex;
      margin: 0 auto;
   }
   @media (max-width: 540px) {
      .menuBody {
         width: 300px;
      }
      .menu {
         display: block;
         div {
            &:last-child {
               width: 100%;
               margin-bottom: 10px;
               a {
                  width: 100%;
               }
            }
         }
      }
      .item {
         display: block;
         div {
            &:first-of-type {
               display: none;
            }
         }
      }
   }
}

.coverMenu {
   background-color: #f8f8f7;
   width: 100vw;
   height: 100vh;
   display: flex;
   position: fixed;
   left: 0;
   top: 0;
   z-index: 100;
   .logo {
      display: none;
   }
   .menuBody {
      h2,
      p {
         display: none;
      }
      .themeBtn {
         margin-top: 30px;
         left: 25px;
      }
   }
   @media (max-width: 540px) {
      .menuBody {
         width: 300px;
      }
      .menu {
         display: block;
         div {
            &:last-child {
               width: 100%;
               margin-bottom: 10px;
               a {
                  width: 100%;
               }
            }
         }
      }
      .item {
         display: block;
         div {
            &:first-of-type {
               display: none;
            }
         }
      }
   }
}

.menuBody {
   width: 446px;
   height: 544px;
   // height: 100%;
   border-radius: 20px;

   text-align: left;

   padding-top: 107px;
   position: relative;

   h2 {
      font-family: "Jost";
      font-style: normal;
      font-weight: 500;
      font-size: 44px;
      line-height: 58px;
      margin-top: 0;
      margin-bottom: 10px;
   }

   p {
      margin: 0px;
      margin-bottom: 60px;
      font-family: "Jost";
      font-style: normal;
      font-weight: 400;
      font-size: 20px;
      line-height: 29px;
      color: #8c8c8c;

      &:last-of-type {
         font-weight: 500;
         color: #1d1d1d;
         margin-bottom: 20px;
      }
   }

   .themeBtn {
      position: absolute;
      display: inline-flex;
      padding: 4px;
      width: 44px;
      height: 24px;
      background: #fbe0dc;
      border-radius: 50px;
      right: 0px;
      top: 0px;

      justify-content: end;
      button {
         display: inline-block;
         width: 16px;
         height: 16px;
         font-size: 0;

         background: #ff868e;
         border-radius: 50%;
      }

      &::before {
         content: "";
         width: 24px;
         height: 24px;
         border-radius: 50%;
         background: #fff url(../../assets/eye.svg) no-repeat center center;

         position: absolute;
         left: -29px;
         top: 0px;
      }
   }

   .closeMenuBtn {
      display: none;
   }
}

.coverMenu {
   .closeMenuBtn {
      @media (max-width: 1230px) {
         display: inline-block;
         position: absolute;
         width: 60px;
         height: 60px;

         background: #ffffff url(../../assets/icons/cross.svg) no-repeat center center;
         border-radius: 20px;
         font-size: 0;
         top: 30px;
         right: -140px;
      }
      @media (max-width: 800px) {
         right: 0px;
      }
   }
}

.menu {
   display: flex;
   justify-content: space-between;
}

.item {
   display: inline-block;

   div {
      height: 198px;
      width: 138px;
      border: 4px solid #ffffff99;

      border-radius: 20px;
      box-sizing: border-box;

      display: flex;
      justify-content: center;
      align-items: center;

      &:last-child {
         display: inline-block;
         background: #ffffff;
         border: none;

         width: 138px;
         height: 36px;
         border-radius: 10px;

         margin-top: 10px;
         text-align: center;
         a {
            text-decoration: none;
            font-family: "Jost";
            font-style: normal;
            font-weight: 500;
            font-size: 12px;
            line-height: 16px;
            /* identical to box height, or 133% */
            width: 100%;
            height: 100%;
            letter-spacing: 2px;

            color: #ff868e;

            display: inline-flex;
            justify-content: center;
            align-items: center;
         }
      }
      &:last-child:hover {
         background: #fbe0dc;
         cursor: pointer;
      }

      &:last-child.active {
         background: #ff868e;
         a {
            color: #fff;
         }
      }
   }
}

//BLACK THEME -------------------------------------------------------------------------------------------------
$bg-black: rgba(255, 255, 255, 0.1);
.menuPage.black {
   .menuBody {
      p {
         &:last-of-type {
            color: #fff;
         }
      }
      .menu {
         .item {
            div {
               background: $bg-black;
            }
            div.active {
               background: #ff868e;
            }
         }
      }

      .themeBtn {
         background: $bg-black;
         justify-content: start;
         &::before {
            background: $bg-black url(../../assets/eye_black.svg) no-repeat center center;
         }
      }
   }
}

.coverMenu.black {
   background-color: #1d1d1d;
   .closeMenuBtn {
      background-color: rgba(255, 255, 255, 0.05);
   }
}
